export default {
  layout: 'home',
  data() {
    return {
      character: {
        left: 50,
        top: 100,
      },
    }
  },
  methods: {
    MoveCharacterRight() {
      this.character.left = this.character.left + 50
    },
    MoveCharacterLeft() {
      this.character.left = this.character.left - 50
    },
    MoveCharacterDown() {
      this.character.top = this.character.top + 50
    },
    MoveCharacterUp() {
      this.character.top = this.character.top - 50
    },
    RestartCharacter() {
      this.character.top = this.character.left = 50
    },
  },
}
